<template>
  <section :class="[$style[panelClass], $style[cname]]">
      <h4>-{{title}}-</h4>
      <slot></slot>
  </section>
</template>

<script>
export default {
    props: {
        cname: {
            type: String,
            default: ''
        },
        title: {
            type: String,
            default: ''
        }
    },
    data() {
        return {
            panelClass: 'panel'
        }
    }
}
</script>

<style lang="scss" module>
    @import '../../css/element.scss';
    .panel {
        @include panel;
    }
</style>




